.letter-game {
  width: 100%;
  max-width: 800px;
  margin: 0 auto;
  padding: 20px;
  position: relative;
}

.game-header {
  display: flex;
  justify-content: space-between;
  align-items: center;
  margin-bottom: 30px;
  background: rgba(255, 255, 255, 0.95);
  padding: 20px;
  border-radius: 20px;
  box-shadow: 0 10px 30px rgba(0, 0, 0, 0.1);
}

.back-btn {
  font-size: 16px;
  padding: 10px 20px;
}

.game-title {
  font-family: 'Fredoka One', cursive;
  font-size: 2rem;
  color: #333;
  margin: 0;
}

.score {
  font-family: 'Fredoka One', cursive;
  font-size: 1.2rem;
  color: #ff6b6b;
  background: white;
  padding: 10px 20px;
  border-radius: 15px;
  box-shadow: 0 5px 15px rgba(255, 107, 107, 0.3);
}

.game-modes {
  display: flex;
  justify-content: center;
  gap: 20px;
  margin-bottom: 40px;
}

.mode-btn {
  padding: 12px 25px;
  font-size: 16px;
}

.mode-btn.active {
  background: linear-gradient(45deg, #feca57, #ff9ff3);
  box-shadow: 0 8px 20px rgba(254, 202, 87, 0.4);
}

.letter-display {
  display: flex;
  justify-content: center;
  margin-bottom: 40px;
}

.letter-card {
  background: white;
  border-radius: 30px;
  padding: 50px;
  text-align: center;
  cursor: pointer;
  transition: all 0.4s ease;
  box-shadow: 0 20px 40px rgba(0, 0, 0, 0.1);
  position: relative;
  overflow: hidden;
  min-width: 300px;
  border: 5px solid transparent;
}

.letter-card:hover {
  transform: translateY(-10px) scale(1.05);
  box-shadow: 0 30px 60px rgba(0, 0, 0, 0.15);
  border-color: #ff6b6b;
}

.letter-card:active {
  animation: wiggle 0.5s ease-in-out;
}

.letter-main {
  font-size: 8rem;
  font-family: 'Fredoka One', cursive;
  color: #ff6b6b;
  text-shadow: 3px 3px 6px rgba(255, 107, 107, 0.3);
  margin-bottom: 20px;
  animation: pulse 2s ease-in-out infinite;
}

.letter-image {
  font-size: 4rem;
  margin-bottom: 20px;
  animation: bounce 2s ease-in-out infinite;
}

.letter-word {
  font-family: 'Fredoka One', cursive;
  font-size: 2rem;
  color: #4ecdc4;
  margin-bottom: 15px;
  text-shadow: 2px 2px 4px rgba(78, 205, 196, 0.3);
}

.tap-hint {
  font-size: 1rem;
  color: #666;
  font-weight: 600;
  background: rgba(255, 255, 255, 0.8);
  padding: 8px 15px;
  border-radius: 20px;
  display: inline-block;
}

.navigation {
  display: flex;
  justify-content: space-between;
  align-items: center;
  margin-bottom: 30px;
}

.nav-btn {
  padding: 15px 25px;
  font-size: 16px;
}

.nav-btn:disabled {
  opacity: 0.5;
  cursor: not-allowed;
  transform: none !important;
}

.progress {
  font-family: 'Fredoka One', cursive;
  font-size: 1.5rem;
  color: white;
  background: linear-gradient(45deg, #667eea, #764ba2);
  padding: 15px 30px;
  border-radius: 20px;
  box-shadow: 0 8px 20px rgba(118, 75, 162, 0.3);
}

.progress-bar {
  width: 100%;
  height: 10px;
  background: rgba(255, 255, 255, 0.3);
  border-radius: 5px;
  overflow: hidden;
  margin-bottom: 30px;
}

.progress-fill {
  height: 100%;
  background: linear-gradient(90deg, #ff6b6b, #4ecdc4, #feca57);
  border-radius: 5px;
  transition: width 0.5s ease;
  animation: shimmer 2s ease-in-out infinite;
}

@keyframes shimmer {
  0% { background-position: -200px 0; }
  100% { background-position: 200px 0; }
}

.floating-letters {
  position: absolute;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
  pointer-events: none;
  z-index: -1;
}

.floating-letter {
  position: absolute;
  font-size: 2rem;
  opacity: 0.2;
  animation: float 8s ease-in-out infinite;
}

.floating-letter:nth-child(1) {
  top: 20%;
  left: 10%;
  animation-delay: 0s;
}

.floating-letter:nth-child(2) {
  top: 60%;
  right: 15%;
  animation-delay: 2s;
}

.floating-letter:nth-child(3) {
  top: 80%;
  left: 20%;
  animation-delay: 4s;
}

.floating-letter:nth-child(4) {
  top: 40%;
  right: 25%;
  animation-delay: 6s;
}

/* Mobile Responsive */
@media (max-width: 768px) {
  .game-header {
    flex-direction: column;
    gap: 15px;
    text-align: center;
  }
  
  .game-title {
    font-size: 1.5rem;
  }
  
  .letter-card {
    padding: 30px;
    min-width: 250px;
  }
  
  .letter-main {
    font-size: 6rem;
  }
  
  .letter-image {
    font-size: 3rem;
  }
  
  .letter-word {
    font-size: 1.5rem;
  }
  
  .navigation {
    flex-direction: column;
    gap: 20px;
  }
  
  .progress {
    order: -1;
  }
}

@media (max-width: 480px) {
  .letter-card {
    padding: 20px;
    min-width: 200px;
  }
  
  .letter-main {
    font-size: 4rem;
  }
  
  .letter-image {
    font-size: 2.5rem;
  }
  
  .letter-word {
    font-size: 1.2rem;
  }
  
  .nav-btn {
    padding: 12px 20px;
    font-size: 14px;
  }
  
  .game-modes {
    flex-direction: column;
    align-items: center;
    gap: 10px;
  }
}

