@import url('https://fonts.googleapis.com/css2?family=Fredoka+One:wght@400&family=Nunito:wght@400;600;700;800&display=swap');

* {
  margin: 0;
  padding: 0;
  box-sizing: border-box;
}

body {
  font-family: 'Nunito', sans-serif;
  background: linear-gradient(135deg, #667eea 0%, #764ba2 100%);
  min-height: 100vh;
  overflow-x: hidden;
}

.App {
  min-height: 100vh;
  display: flex;
  flex-direction: column;
}

.game-container {
  flex: 1;
  padding: 20px;
  display: flex;
  justify-content: center;
  align-items: center;
}

/* Cartoon Button Styles */
.cartoon-button {
  background: linear-gradient(45deg, #ff6b6b, #ff8e8e);
  border: none;
  border-radius: 25px;
  padding: 15px 30px;
  font-family: 'Fredoka One', cursive;
  font-size: 18px;
  color: white;
  cursor: pointer;
  box-shadow: 0 8px 15px rgba(255, 107, 107, 0.3);
  transform: translateY(0);
  transition: all 0.3s ease;
  position: relative;
  overflow: hidden;
}

.cartoon-button:hover {
  transform: translateY(-3px);
  box-shadow: 0 12px 20px rgba(255, 107, 107, 0.4);
  background: linear-gradient(45deg, #ff5252, #ff7979);
}

.cartoon-button:active {
  transform: translateY(-1px);
  box-shadow: 0 5px 10px rgba(255, 107, 107, 0.3);
}

.cartoon-button.blue {
  background: linear-gradient(45deg, #4ecdc4, #44a08d);
  box-shadow: 0 8px 15px rgba(78, 205, 196, 0.3);
}

.cartoon-button.blue:hover {
  background: linear-gradient(45deg, #26d0ce, #2dd4bf);
  box-shadow: 0 12px 20px rgba(78, 205, 196, 0.4);
}

.cartoon-button.yellow {
  background: linear-gradient(45deg, #feca57, #ff9ff3);
  box-shadow: 0 8px 15px rgba(254, 202, 87, 0.3);
}

.cartoon-button.yellow:hover {
  background: linear-gradient(45deg, #ff6348, #ff9ff3);
  box-shadow: 0 12px 20px rgba(254, 202, 87, 0.4);
}

.cartoon-button.green {
  background: linear-gradient(45deg, #55a3ff, #003d82);
  box-shadow: 0 8px 15px rgba(85, 163, 255, 0.3);
}

.cartoon-button.green:hover {
  background: linear-gradient(45deg, #48dbfb, #0abde3);
  box-shadow: 0 12px 20px rgba(85, 163, 255, 0.4);
}

/* Card Styles */
.game-card {
  background: white;
  border-radius: 20px;
  padding: 30px;
  box-shadow: 0 10px 30px rgba(0, 0, 0, 0.1);
  margin: 10px;
  transition: all 0.3s ease;
  cursor: pointer;
  position: relative;
  overflow: hidden;
}

.game-card:hover {
  transform: translateY(-5px);
  box-shadow: 0 15px 40px rgba(0, 0, 0, 0.15);
}

.game-card::before {
  content: '';
  position: absolute;
  top: 0;
  left: 0;
  right: 0;
  height: 5px;
  background: linear-gradient(90deg, #ff6b6b, #4ecdc4, #feca57);
}

/* Animation Classes */
.bounce {
  animation: bounce 0.6s ease-in-out;
}

@keyframes bounce {
  0%, 20%, 50%, 80%, 100% {
    transform: translateY(0);
  }
  40% {
    transform: translateY(-10px);
  }
  60% {
    transform: translateY(-5px);
  }
}

.wiggle {
  animation: wiggle 0.5s ease-in-out;
}

@keyframes wiggle {
  0%, 100% { transform: rotate(0deg); }
  25% { transform: rotate(-3deg); }
  75% { transform: rotate(3deg); }
}

.pulse {
  animation: pulse 1s ease-in-out infinite;
}

@keyframes pulse {
  0% { transform: scale(1); }
  50% { transform: scale(1.05); }
  100% { transform: scale(1); }
}

.fade-in {
  animation: fadeIn 0.5s ease-in;
}

@keyframes fadeIn {
  from { opacity: 0; transform: translateY(20px); }
  to { opacity: 1; transform: translateY(0); }
}

/* Responsive Design */
@media (max-width: 768px) {
  .game-container {
    padding: 10px;
  }
  
  .cartoon-button {
    padding: 12px 24px;
    font-size: 16px;
  }
  
  .game-card {
    padding: 20px;
    margin: 5px;
  }
}

@media (max-width: 480px) {
  .cartoon-button {
    padding: 10px 20px;
    font-size: 14px;
  }
  
  .game-card {
    padding: 15px;
  }
}

/* Star decorations */
.stars {
  position: fixed;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
  pointer-events: none;
  z-index: -1;
}

.star {
  position: absolute;
  background: #fff;
  border-radius: 50%;
  animation: twinkle 2s ease-in-out infinite alternate;
}

@keyframes twinkle {
  from { opacity: 0.3; transform: scale(0.8); }
  to { opacity: 1; transform: scale(1.2); }
}

.star:nth-child(1) { top: 10%; left: 20%; width: 3px; height: 3px; animation-delay: 0s; }
.star:nth-child(2) { top: 20%; left: 80%; width: 2px; height: 2px; animation-delay: 0.5s; }
.star:nth-child(3) { top: 60%; left: 10%; width: 4px; height: 4px; animation-delay: 1s; }
.star:nth-child(4) { top: 80%; left: 70%; width: 2px; height: 2px; animation-delay: 1.5s; }
.star:nth-child(5) { top: 30%; left: 50%; width: 3px; height: 3px; animation-delay: 2s; }

