.number-game {
  width: 100%;
  max-width: 900px;
  margin: 0 auto;
  padding: 20px;
  position: relative;
}

.number-display {
  display: flex;
  justify-content: center;
  margin-bottom: 40px;
}

.number-card {
  background: white;
  border-radius: 30px;
  padding: 50px;
  text-align: center;
  cursor: pointer;
  transition: all 0.4s ease;
  box-shadow: 0 20px 40px rgba(0, 0, 0, 0.1);
  position: relative;
  overflow: hidden;
  min-width: 350px;
  border: 5px solid transparent;
}

.number-card:hover {
  transform: translateY(-10px) scale(1.05);
  box-shadow: 0 30px 60px rgba(0, 0, 0, 0.15);
  border-color: #4ecdc4;
}

.number-card:active {
  animation: wiggle 0.5s ease-in-out;
}

.number-main {
  font-size: 10rem;
  font-family: 'Fredoka One', cursive;
  color: #4ecdc4;
  text-shadow: 3px 3px 6px rgba(78, 205, 196, 0.3);
  margin-bottom: 20px;
  animation: pulse 2s ease-in-out infinite;
}

.number-emoji {
  font-size: 4rem;
  margin-bottom: 20px;
  animation: bounce 2s ease-in-out infinite;
}

.counting-items {
  display: flex;
  flex-wrap: wrap;
  justify-content: center;
  gap: 10px;
  margin: 20px 0;
  min-height: 60px;
  align-items: center;
}

.counting-item {
  font-size: 2rem;
  animation: popIn 0.5s ease-out forwards;
  opacity: 0;
  transform: scale(0);
}

@keyframes popIn {
  to {
    opacity: 1;
    transform: scale(1);
  }
}

.number-word {
  font-family: 'Fredoka One', cursive;
  font-size: 2.5rem;
  color: #ff6b6b;
  margin-bottom: 15px;
  text-shadow: 2px 2px 4px rgba(255, 107, 107, 0.3);
}

.quiz-display {
  background: white;
  border-radius: 30px;
  padding: 40px;
  margin-bottom: 40px;
  box-shadow: 0 20px 40px rgba(0, 0, 0, 0.1);
}

.quiz-question {
  text-align: center;
  margin-bottom: 30px;
}

.quiz-question h3 {
  font-family: 'Fredoka One', cursive;
  font-size: 2rem;
  color: #333;
  margin-bottom: 20px;
}

.quiz-options {
  display: grid;
  grid-template-columns: repeat(auto-fit, minmax(150px, 1fr));
  gap: 20px;
  margin-top: 30px;
}

.quiz-option {
  padding: 20px;
  border-radius: 20px;
  display: flex;
  flex-direction: column;
  align-items: center;
  gap: 10px;
  transition: all 0.3s ease;
  min-height: 120px;
  justify-content: center;
}

.quiz-option:disabled {
  cursor: not-allowed;
}

.quiz-option.correct {
  background: linear-gradient(45deg, #2ed573, #7bed9f) !important;
  animation: correctAnswer 0.6s ease-in-out;
}

.quiz-option.incorrect {
  background: linear-gradient(45deg, #ff6b6b, #ff8e8e) !important;
  animation: incorrectAnswer 0.6s ease-in-out;
}

@keyframes correctAnswer {
  0%, 100% { transform: scale(1); }
  50% { transform: scale(1.1); }
}

@keyframes incorrectAnswer {
  0%, 100% { transform: translateX(0); }
  25% { transform: translateX(-5px); }
  75% { transform: translateX(5px); }
}

.option-number {
  font-size: 2.5rem;
  font-family: 'Fredoka One', cursive;
  color: white;
}

.option-word {
  font-size: 1.2rem;
  font-weight: 600;
  color: white;
}

.floating-numbers {
  position: absolute;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
  pointer-events: none;
  z-index: -1;
}

.floating-number {
  position: absolute;
  font-size: 2rem;
  opacity: 0.2;
  animation: float 8s ease-in-out infinite;
}

.floating-number:nth-child(1) {
  top: 15%;
  left: 5%;
  animation-delay: 0s;
}

.floating-number:nth-child(2) {
  top: 70%;
  right: 10%;
  animation-delay: 2s;
}

.floating-number:nth-child(3) {
  top: 85%;
  left: 15%;
  animation-delay: 4s;
}

.floating-number:nth-child(4) {
  top: 30%;
  right: 20%;
  animation-delay: 6s;
}

/* Mobile Responsive */
@media (max-width: 768px) {
  .number-card {
    padding: 30px;
    min-width: 280px;
  }
  
  .number-main {
    font-size: 7rem;
  }
  
  .number-emoji {
    font-size: 3rem;
  }
  
  .counting-item {
    font-size: 1.5rem;
  }
  
  .number-word {
    font-size: 2rem;
  }
  
  .quiz-question h3 {
    font-size: 1.5rem;
  }
  
  .quiz-options {
    grid-template-columns: repeat(auto-fit, minmax(120px, 1fr));
    gap: 15px;
  }
  
  .quiz-option {
    padding: 15px;
    min-height: 100px;
  }
  
  .option-number {
    font-size: 2rem;
  }
  
  .option-word {
    font-size: 1rem;
  }
}

@media (max-width: 480px) {
  .number-card {
    padding: 20px;
    min-width: 240px;
  }
  
  .number-main {
    font-size: 5rem;
  }
  
  .number-emoji {
    font-size: 2.5rem;
  }
  
  .counting-item {
    font-size: 1.2rem;
  }
  
  .number-word {
    font-size: 1.5rem;
  }
  
  .quiz-options {
    grid-template-columns: 1fr;
    gap: 10px;
  }
  
  .quiz-option {
    padding: 12px;
    min-height: 80px;
  }
  
  .option-number {
    font-size: 1.5rem;
  }
  
  .option-word {
    font-size: 0.9rem;
  }
}

